import React from 'react';
import ReactDOM from 'react-dom';
import ValidationForm from '../ValidationForm';

class EntandoLapamMetopackConfig extends HTMLElement {
    constructor() {
        super();
        this.reactRootRef = React.createRef();
        this.mountPoint = null;
    }

    get config() {
        const newVar = this.reactRootRef.current ? this.reactRootRef.current.state : {};
        console.log("invocato config get",newVar)
        return newVar;
    }

    set config(value) {
        console.log("invocato config set",value)
        return this.reactRootRef.current.setState(value);
    }

    connectedCallback() {
        this.mountPoint = document.createElement('div');
        this.appendChild(this.mountPoint);
        ReactDOM.render(<ValidationForm ref={this.reactRootRef} />, this.mountPoint);
    }
}

customElements.define('entando-lapam-metopack-config', EntandoLapamMetopackConfig);
