import React from 'react';
import {Form, Grid, Button} from 'patternfly-react';

export default class ValidationForm extends React.Component {
    constructor(props) {
        super(props);
        this.defaultValue='report/bilancioPeriodico'

        this.state = {
            modulo: this.defaultValue,
        };
        this.handleChange= (event)=> {
            this.setState({modulo: event.target.value});
        }
    }

    render() {
        return (
            <Form horizontal>
                <Form.FormGroup controlId="name">
                    <Grid.Col componentClass={Form.ControlLabel} sm={3}>
                        Modulo Funzionale
                    </Grid.Col>
                    <Grid.Col sm={9}>
                        <select value={this.state.modulo} onChange={this.handleChange}>
                            <option value={this.defaultValue}>bilancioPeriodico</option>
                        </select>
                        <Form.HelpBlock>Scegli il modulo funzionale da caricare</Form.HelpBlock>
                    </Grid.Col>
                </Form.FormGroup>
                <Grid.Row style={{paddingTop: '10px', paddingBottom: '10px'}}>
                    <Grid.Col smOffset={3} sm={9}>
                    </Grid.Col>
                </Grid.Row>
            </Form>)
    }
}
